.main-transition-enter {
  opacity: 0.01;
  transform: scale(1.1);
}

.main-transition-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 300ms;
}

.main-transition-exit {
  opacity: 1;
  transform: scale(1);
}

.main-transition-exit-active {
  opacity: 0.01;
  transform: scale(1.1);
  transition: all 300ms;
}

.about-me-transition-transition-enter {
  opacity: 0.01;
  transform: scale(1.1);
}

.about-me-transition-transition-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 300ms;
}

.about-me-transition-transition-exit {
  opacity: 1;
  transform: scale(1);
}

.about-me-transition-transition-exit-active {
  opacity: 0.01;
  transform: scale(1.1);
  transition: all 300ms;
}

.about-me-filter {
  opacity: .8;
  min-width: 73.375%;
  min-height: 72.33%;
  position: absolute;
  z-index: -400;
  background: linear-gradient(25deg,#5b7bad 5%, #486693 90%);
  margin: -2px 0 0 -3.5px;
}

.about-me-left-col {
  background: transparent;
  padding: 0;
  min-height: 60vh;
  text-align: right;
  margin: 2.5vh 2%;
  z-index: 500;
  border: 15px inset #6582a3;
}

.about-me-right-col {
  padding: 10px;
  margin: 0 0 0 2.25vw;
  z-index: 500;
  border-left: 1.5px solid black;
}

.about-me-view {
  background: transparent;
