.App {
  margin: 0;
  padding: 0;
  min-width: 100%;
  min-height: 100%;
}

.App header {
  font-size: 6vw;
  letter-spacing: 15px;
  line-height: 1;
  font-family: "Work Sans";
  font-weight: 200;
  color: ghostwhite;
  text-shadow:  -5px 2px 5px black;
}

.App .header-container {
  cursor: pointer;
  font-family: "Londrina Shadow";
