body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg,#6486ba 25%, #87CEEB 70%, #425c84);
  min-height: 100vh;
  min-width: 100vw;
  text-align: center;
  overflow: hidden;
}

.body-filter {
  min-height: 100vh;
  position: absolute;
  min-width: 100vw;
  z-index: -2000;
  opacity: 0;
  background: #2ea045;
}

.card {
  margin: 2.5% 0;
  z-index: 500;
  position: absolute;
  width: 70%;
  height: 80%;
  background: transparent;
  border: 10px inset #6582a3;
}


.card .card-text {
  color: white;
  font-family: "Work Sans";
  font-size: 1.45vw;
  font-weight: 200;
  letter-spacing: .0125vw;
  padding: .5vh 2vw 2vh 2vw;
  text-align: left;
}

.card .card-title {
  color: white;
  font-family: "Work Sans";
  font-feature-settings: "kern" off;
  font-kerning: none;
  font-size: 2.75vw;
  font-weight: 300;
  letter-spacing: .2vw;
  line-height: 2.75vw;
  margin: 1.25vh 0 1.75vh 0;
}

.card-close-button {
  position: absolute;
  top: 5%;
  margin: 0 0 0 27.5%;
  width: 18.5%;
  height: 60px;
  font-size: 1.5vw;
  letter-spacing: .15vw;
  font-weight: 300;
  font-family: "Segoe UI", sans-serif;
  border: 5px outset #6582a3;
  padding: 0;
  z-index: 100;
  border-radius: 2.5%;
  border: 7.5px outset;
  border-image: linear-gradient(10deg, #6f7e99 0%, #6185a0 50%, #6582a3 100%);
  border-image-slice: .45;
  background: linear-gradient(35deg,#516c96 5%, #425c84 90%);
}

.card-close-button:hover {
  border: 7.5px inset;
}

