.grid-button{

  border-radius: 2.5%;
  border: 10px outset;
  border-image: linear-gradient(10deg, #6f7e99 0%, #6185a0 50%, #6582a3 100%);
  border-image-slice: .45;
  background: linear-gradient(35deg,#516c96 5%, #425c84 90%);
  height: 8vh;
  font-size: 1.6vw;
  font-weight: 200;
  font-family: "Work Sans", sans-serif;
  line-height: 1.65vw;
  padding: 0 25px;
  letter-spacing: 1.5px;
  text-shadow: 3px 1px 3px black;
  vertical-align: top;
  width: 18.5%;
  z-index: 100;
  /*
    min-width: -------------
    max-width:
    width:       Set in component
    margin:         render
    min-height:
    max-height:
    visibility:_____________
  */
}

.grid-button:hover {
  text-shadow: 0px 0px 2.5px ghostwhite;
  border: 5px inset ghostwhite;
}

.grid-filter{
  background: linear-gradient(25deg,#5b7bad 5%, #486693 90%);
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  opacity: 0.75;
  z-index: -100;
  margin: -2.5vh -2.5vw;
}

.main-row {
  margin: 0;
  display: contents ;
}

.main-row-col {
  float: left;
  padding: 0;
  color: black;
  margin-bottom: 2.5vh;
  box-shadow: 2px 2px 7px 1px #98a9bc;
  border-radius: .75%;
}

.main-row-col-filter{
  border: 1px solid white;
  border-radius: .75%;
  margin-bottom: 2.5vh;
  height: 100%;
  opacity: .6;
  height: 13.75vh;
  z-index: -1000;
  background: linear-gradient(-120deg, #7a8aa8 0%, #6b8ba8 50%, #4f657f 100%);
  transition: .5s ease-in-out;
}

.main-row-col button {
  width: 100%;
  min-width: 100%;
  height: 13.75vh;
  background: transparent;
  border-radius: 0;
  border: 7.5px outset;
  border-image: linear-gradient(10deg, #6f7e99 0%, #6185a0 50%, #6582a3 100%);
  border-image-slice: 1;
}

.main-row-col button:hover {
  background: transparent;
  border: 7.5px outset;
  border-image: linear-gradient(10deg, #6f7e99 0%, #6185a0 50%, #6582a3 100%);
  border-image-slice: 1;
}

.main-row-col:hover + .main-row-col-filter {
  opacity: .9;
}

.main-row-col button p {
  font-size: 3vw;
  letter-spacing: 2.5px;
  font-family: "Work Sans", serif;
  font-weight: 100;
  text-shadow: -2.5px -1px 5px black;
}

.profile-image{
  min-width: 80%;
  max-width: 80%;
  border: 25px inset #6582a3;
  margin: 0 auto;
  z-index: 1000;
}

.profile-image-container {
  margin: 0;
  min-height: 35vh ;
  min-width: 100%;
  padding: 25px;
}

.project-card {
  margin: 2.5% 0;
  z-index: 500;
  position: absolute;
  width: 70%;
  height: 80%;
  border: 17.5px inset #6582a3;
  background: linear-gradient(35deg,#516c96 5%, #425c84 90%);
}

.project-card .card-body {
  overflow-y: scroll;
}

.project-card .card-body .card-text {
  text-align: center;
}

.project-description {
  border-top: 1px solid white;
  padding: 2.5vh 5% 0 5%;
  text-align: center;
}

.projects-grid {
  border: 17.5px outset white;
  padding: 2.5vh 2.5vw;
  height: 70vh;
  min-height: 70vh;
  max-height: 70vh;
}

.projects-row {
  margin: 0;
}

.project-date {
  color: white;
  font-size: 1.1vw;
  font-weight: 100;
  letter-spacing: 1px;
}

.project-link-button {
  background: linear-gradient(35deg,#516c96 5%, #425c84 90%);
  border: 5px outset;
  border-image: linear-gradient(10deg, #6f7e99 0%, #6185a0 50%, #6582a3 100%);
  border-image-slice: 1;
  font-weight: 200;
  margin: 2vh 5%;
  min-width: 30%;
  transition: 2s ease-in-out;
  letter-spacing: 3.15px;
}

.project-link-button > a {
  color: white;
}



.project-tag-container {
  display: flex;
  height: fit-content;
  list-style: none;
  margin: 0;
  min-height: fit-content;
  padding: 0;
  text-align: center;
  flex-wrap: wrap-reverse;
  border-top: 1px solid white;
  border-bottom: 1px solid white;
}

.project-tags {
  display: inline-block;
  float: left;
  line-height: 1.75vw;
  padding: 5px 10px 5px 10px;
  margin: 0 auto 0 auto;
  max-width: fit-content;
}


.root {
  min-height: 100vh;
}

.section-content {
  padding: 0 20px;
}

.section-content h4 {
  font-family: "Work Sans";
  padding: 15px 5px 2.5px 15px;
  font-size: 32px;
  font-weight: 300;
  letter-spacing: 2.25px;
  max-width: fit-content;
  color: ghostwhite;
  font-feature-settings: "pcap";
  margin: 10px 5% 0px auto;
  min-height: fit-content;
  max-height: 10vh;
  text-shadow: 3px 2px 7.5px black;
  border-bottom: 3.5px ridge #6582a3;
}

.section-content p {
  color: ghostwhite;
  font-weight: 400;
  font-size: 1.1vw;
  letter-spacing: 1.8px;
  font-feature-settings: "kern" off;
  font-kerning: none;
  margin: 3.5vh .5vw 1.5vh .5vw;
  line-height: 1.65vw;
  min-height: 34vh;
  max-height: 34vh;
  padding: 2.55vh 1.5vw 2.55vh 2vw;
  text-align: left;
  text-indent: 30px;
  text-shadow: -3.5px 1.5px 4px black;
  border: 10px ridge ghostwhite;
  overflow-y: scroll;
}

.section-option, .section-option button {
  font-size: 22px;
  font-weight: 300;
  letter-spacing: .9px;
  font-variant: petite-caps;
  background-color: transparent;
  border-radius: 0;
  min-width: 89%;
  margin: 0 auto;
}

.section-option button {
  background: linear-gradient(35deg,#516c96 5%, #425c84 90%);
  padding: 0;
  font-size: 1.3vw;
  letter-spacing: 2.25px;
  box-shadow: 2px 2px 3px 0.25px black;
  border: 5px outset #6582a3;
  transition: .3s linear;
}

.section-option button:hover {
  background: linear-gradient(35deg,#516c96 5%, #425c84 90%);
  border: 7.5px inset ghostwhite;
}


.section-options-container {
  margin: 0;
  min-height: 35vh;
  padding: .5vh 0 0 0;
}

.section-tabs{
  border: none;
  text-align: right;
  display: inline-block;
  margin: 0 0 7.5px 0;
}

.section-tabs > .nav-item {
  margin: 0 0 0px 15px;
  background: linear-gradient(35deg,#516c96 5%, #425c84 90%);
  border: 5px outset #6582a3;
  color: white;
  display: inline-block;
  float: none;
  font-size: 18px;
  font-feature-settings: "pcap", "c2pc";
  font-variant: all-petite-caps;
  font-weight: 300;
  letter-spacing: 1.1px;
  margin-bottom: 0;
  text-shadow: 3.5px 1.5px 4px black;
  zoom: 1;
  transition: .2s ease-in-out;
}

.section-tabs > .nav-item:hover {
  border: 5px inset #6582a3;
}

.section-tabs > .nav-item:hover > a {
  border: none;
  padding: 0.5rem 1rem;
  margin: 1px;
  cursor: pointer;
}

.section-tabs-container {
  display: inline-block;
  min-width: 90%;
  padding: 10px 0 0 0;
  margin: 0px 30px 0 1.25vw;
