.main-transition-enter {
  opacity: 0.01;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.main-transition-enter-active {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: all 300ms;
}

.main-transition-exit {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.main-transition-exit-active {
  opacity: 0.01;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: all 300ms;
}

